import React from 'react'
import Logo from '../Logo';
import Search from '../Search';
import Menu from '../Menu';

const header = {
  height: '42px',
    position: 'relative',
    width: '100%',
    backgroundColor: '#4267b2',
    display: 'flex',
    padding: '0px 205px 0px 90px'
}
const Navbar = () => {
  return (
    <header style={header}>
        <Logo />
        <Search />
        <Menu />
    </header>
  )
}

export default Navbar
