import React from 'react'

const Logo = () => {
  const logo = {
    height: '24px',
    outline: 'none',
    overflow: 'hidden',
    textIndent: '-999px',
    whiteSpace: 'nowrap',
    width: '24px',
  
    margin: '10px '
  }
  return (
    <div>
      <img style={logo} src="https://static.xx.fbcdn.net/rsrc.php/v3/ym/r/Gjhrhb7r0lb.png?_nc_eui2=AeGk5eQhW0qD3NN6W4QOEhJrrnC626sZnnCucLrbqxmecEDP21MvhJMIWi9MiCp8hyhPX5sKmOez9PfKRA3zcU1o" />
    </div>
  )
}

export default Logo
