import React from 'react'
import Home from '../Home';
import {FaUserFriends,FaFacebookMessenger,FaUsers } from 'react-icons/fa'
import {IoMdNotifications} from 'react-icons/io'
import { RiUserAddLine } from 'react-icons/ri'
import { BsQuestionCircleFill } from 'react-icons/bs'
import {GoTriangleDown}  from 'react-icons/go'
const pdp = 'https://scontent.ftnr2-1.fna.fbcdn.net/v/t1.0-0/cp0/e15/q65/p320x320/58543643_10211903032285738_2987565356878397440_o.jpg?_nc_cat=105&_nc_sid=85a577&efg=eyJpIjoiYiJ9&_nc_eui2=AeFWGuwEv4Y1bkKQm_qlClkrzCky8GnNl3rMKTLwac2Xeo4QSfC9k-weWFzsVhWm0uCor7BJuF6B3cLj04qKPPpo&_nc_ohc=2VHmV6lpZeQAX9W3VF7&_nc_ht=scontent.ftnr2-1.fna&_nc_tp=3&oh=a2d930100e78b402cba5858d7e00ed03&oe=5ECF6B97'
const Menu = () => {
  const menu = {
    main: {
      marginLeft: '100px',
      display: 'flex',
      justifyContent: 'center',
      alignItems:'center'
    },
    pdp: {
      height: '25px',
      width: '25px',
      borderRadius :'100%',
      marginTop: '8px',
     
    },
    nom:{
      color: 'inherit',
      display: 'inline-block',
      fontSize: '12px',
      fontWeight: 'bold',
      color: 'white',
      fontFamilly: 'sans-serif',
      padding: '5px  11px',
      marginTop: '8px',  
      
      height: '20px'
    },
    icon:{
      color: 'inherit',
      display: 'inline-block',
      fontSize: '15px',
      padding: '0px  8px',
      height: '20px'
    }
  }
  return (
    <div style={menu.main}>
        <span  >
          <img src={pdp}  style={menu.pdp} />
        </span>
        <span style={menu.nom}>Todisoa</span>
        <span style={menu.nom}>Home</span>
        <span style={menu.nom}>Create</span>
        <span style={menu.icon}>
          <FaUserFriends size="1.4em" color="#0d1424" />
        </span>
        <span style={menu.icon}>
          <FaFacebookMessenger size="1.4em" color="#0d1424" />
        </span>
        <span style={menu.icon}>
          <IoMdNotifications size="1.4em" color="#0d1424" />
        </span>

        <span style={menu.icon}>
          <FaUsers size="1.4em" color="#0d1424" />
        </span>
        <span style={menu.icon}>
          <BsQuestionCircleFill size="1.4em" color="#0d1424" />
        </span>
        <span style={menu.icon}>
          <GoTriangleDown size="1.3em" color="#0d1424" />
        </span>
    </div>
  )
}

export default Menu
