import React from 'react'
import { AiOutlineSearch } from 'react-icons/ai'

const input = {
  field: {
    padding: '0px 5px 0px 0px',
    fontSize: '14px',
    border: '1px solid grey',
    height: '23px',
    width: '393px',
    background: '#fff',  
    padding: '3px 0 0 10px',
    textShadow: 'none',
    border: 'none',
     borderRadius: ' 2px 0 0 2px ',
    outline: 'none'
  },
  button: {
    
   
    padding: '0px 16px',
    height: '28px',
    width: '46px',
    background: '#f5f6f7',
    borderRadius: '0 5px 5px 0',
    color: 'white',
    fontSize: '17px',
    border: '1px solid grey',
    borderLeft: 'none',
    cursor: 'pointer'
  },
  mainSearch: {
    marginTop: '8px',
    
  }
}








const Search = () => {
  return (
    <div style={input.mainSearch} >
      <input style={input.field} type="text" placeholder="Search.." name="search2" />
        <button style={input.button} type="submit" id="b">
          <AiOutlineSearch color="black" />
        </button>
    </div>
      )
    }
    
    export default Search
