import React from 'react'

const Login = () => {
  const pdp = 'https://scontent.ftnr2-1.fna.fbcdn.net/v/t1.0-0/cp0/e15/q65/p320x320/58543643_10211903032285738_2987565356878397440_o.jpg?_nc_cat=105&_nc_sid=85a577&efg=eyJpIjoiYiJ9&_nc_eui2=AeFWGuwEv4Y1bkKQm_qlClkrzCky8GnNl3rMKTLwac2Xeo4QSfC9k-weWFzsVhWm0uCor7BJuF6B3cLj04qKPPpo&_nc_ohc=2VHmV6lpZeQAX9W3VF7&_nc_ht=scontent.ftnr2-1.fna&_nc_tp=3&oh=a2d930100e78b402cba5858d7e00ed03&oe=5ECF6B97'
  const login = {
    main: {
      display: 'flex',
      justifyContent: 'center',
      alignItem: 'center',
      marginTop: '50px'
    },
    item: {
      width: '500px',
      height: '250px',
      border: '1px solid #dddd',
      borderRadius: '3px',
      padding: '20px'
    },
    legend: {
      fontWeight: 'bold',
      marginLeft: '15px',
      color: '#1a397c'
    },
    pdp: {
      width:' 56px' ,
      height: '56px'
    },
    mainDescription: {
      margin: '10px 15px',
      display: 'flex',
      alignItems: 'center',
      
    },
    username: {
      fontWeight: 'bold',
      color: '#696960',
      marginLeft: '10px'
      
    },
    loginForm: {
      display:'flex',
      justifyContent:'center'
    },
    footer: {
      backgroundColor: '#dddd',
      height: '100px',
      paddingTop: '10px',
      paddingRight: '10px'
      
    }
  }
  return (
    <div style={login.main}>
      <div style={login.item}>
        <form>
          <legend style={login.legend}>Veuillez saisir votre mot de passe pour continuer</legend>
          <hr width="460" />
          <main style={login.mainDescription}>
            <span>
              <img style={login.pdp} src={ pdp} />
            </span>
            <spann style={login.username} >Todisoa Andrianomena</spann>
            
          </main>
          <p>Pour consulter cette page,vous devez confirmer votre mot de passe</p>
          <div style={login.loginForm}>
            <span>Mot de passe</span>
            <span>
              <input type="password" />
            </span>
            
          </div>
          <p style={{textAlign:'center'}}><a href="#">Mot de passe oublié</a></p>
        </form>
        <div style={login.footer}>
          <button style={{background: '#4267b2',padding: '10px 16px',color:'white',borderRadius:'5px',border: 'none',float: 'right'}}>connecter</button>
        </div>
      </div>
     
    </div>
  )
}

export default Login
